.profile{
    display: flex;
}
.profileRight{
    flex: 9;
}
.profileCover{
    height: 320px;
    position: relative;
}
.profileCoverImg{
    width: 100%;
    height: 225px;
    object-fit: cover;
}
.profileUserImg{
    width: 150px;
    height: 150px;
    border: 3px solid white;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    left: 0;
    right: 0;
    top: 150px;
    margin: auto;
}
.profileInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 10px;
}
.profileRightBottom{
    display: flex;
}
.profileline{
color: #555;
}